<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <LeftMenu :userid="loggeduserid" :RecentPinnedFolderArray="RecentFolderArray" :sharedChildFolder="sharedChildFolder"
            :organizationSpace="organizationSpace" :pinnedFolderArray="pinnedFolderArray" :SharedFolder="SharedFolderParent"
            :UserList="SharedUserList" :FolderArray="FindalFolderArray"></LeftMenu>
        <section id="main-content" class="main-content">
            <section class="main-content">
                <div class="right-content hr_jobpost" id="rightMenuRecycle">
                    <div class="col-md-12 p-0 mb-2">
                        <div class="theme-primary partition p-actions-expand">
                            <span class="p-name text-white">{{ $t('RecycleBin') }}</span>
                            <span class="p-actions float-right">
                                <a class="p-action-btn text-white float-right disabled" id="delete"
                                    @click="DeleteSourceFiles">
                                    <em class="fa fa-trash"></em>
                                    <br />{{ $t('Delete') }}
                                </a>
                                <a class="p-action-btn text-white float-right disabled" @click="RestoreFiles" id="Restore">
                                    <em class="fa fa-recycle"></em><br />{{ $t('Restore') }}
                                </a>
                                <a class="p-action-btn text-white float-right" id="EmptyBin"
                                    @click="EmptyBin">
                                    <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor"
                                        class="bi bi-trash" viewBox="0 0 16 16">
                                        <path
                                            d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                                        <path fill-rule="evenodd"
                                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z" />
                                    </svg><br />{{ $t('EmptyBin') }}
                                </a>
                            </span>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-2 searchpanel tablehead search float-left">
                            <div class="w-100 ">
                                <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left  pt-2 p-0">
                                    <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                                        <div class="row mt-0">
                                            <div class="col-7 float-left pr-1 pl-0">
                                                <div class="form-group">
                                                    <input id="search" maxlength="50"
                                                        @keydown.enter="SearchRecycleBinData(searchCondition)"
                                                        v-model="searchCondition" class="form-control search" type="text"
                                                        placeholder="Search by Folder name, File name, Tag ..."
                                                        autocomplete="off">
                                                </div>
                                            </div>
                                            <div class="col-5 float-left pl-0">
                                                <div class="background-0 text-left form-check-inline">
                                                    <input type="hidden" id="hdnSearch" value="">
                                                    <a title="" id="Go" @click="SearchRecycleBinData(searchCondition)"
                                                        href="javascript:;" class="btn btn-success mr-1 px-4 hlsearch"
                                                        data-toggle-tooltip="tooltip" data-original-title="Search"
                                                        aria-describedby="tooltip697211"><i class="fa fa-search"></i></a>
                                                    <a href="javascript:;" title="" @click="RefreshPage"
                                                        class="btn btn-danger clearsearchtext px-4"
                                                        data-toggle-tooltip="tooltip" data-original-title="Clear all "><i
                                                            class="fa fa-refresh"></i></a>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                    </div>
                    <div class="listing">
                        <div class="expand-list">
                            <a href="javascript:;" @click="ExpandCollapseList" class="expand-list-btn justify-content-center align-items-center">
                                <em class="icon-solid arrow-solid-left text-white" style="font-size:9pt"></em>
                            </a>
                        </div>
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblRecycleBinFileList">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="width:50px !important;" v-on:change="onChange()" class="text-center">
                                            <input id="chkAll" class="chkitems" value="" type="checkbox">
                                        </th>
                                        <th @click="SortData($event, 'Name', 'Sort', 'RecycleBin')">
                                            <span class="sort"
                                                :class="{ 'tb_headerSortDown': sortExp == 'ASC' && specificSortClick == 'Name', 'tb_headerSortUp': sortExp == 'DESC' && specificSortClick == 'Name' }">Name</span>
                                        </th>
                                        <th class="text-center">
                                            <span>{{ $t('FileLocation') }}</span>
                                        </th>
                                        <th class="text-center">
                                            <span>{{ $t('DeletedOn') }}</span>
                                        </th>
                                        <th class="text-center">
                                            <span>{{ $t('Size') }}</span>
                                        </th>
                                        <th class="text-center">
                                            <span>{{ $t('RetentionDay') }}</span>
                                        </th>
                                        <th data-width="5%" class="text-center nonresize">
                                            <span>{{ $t('Action') }}</span>
                                        </th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in recyleBinData">
                                        <td class="text-center"><input v-on:change="onChange()" :value="item.Id"
                                                :docCode="item.IsDocCode" v-bind:fileid="item.Id" class="chkItems"
                                                type="checkbox"></td>
                                        <td>
                                            <img style="height: 30px;"
                                        v-if="item.IsDocCode == 1 && (item.DocExtention == 'pptx' || item.DocExtention == 'docx' || item.DocExtention == 'pdf' || item.DocExtention == 'jpg' || item.DocExtention == 'png' || item.DocExtention == 'jpeg' || item.DocExtention == 'xlsx')"
                                        :alt='item.FileType' :title='item.DocType'
                                        :data-fileType='item.DocExtention' :src="GetThumbnail(item.FileThumbnailPath)" />
                                            <em v-if="item.IsDocCode == 1" 
                                            :class="GetFileIconClass(item.DocExtention)" width="27px"></em> &nbsp;
                                            <em class="fa fa-folder pr-2 font-18 text-primary" v-if="item.IsDocCode == 0"
                                                aria-hidden="true"></em>
                                            <span class="text-center text-overflow-dynamic-container">
                                                {{ item.NAME }}
                                            </span>
                                        </td>
                                        <td class="text-center">
                                            <span class="text-center text-overflow-dynamic-container">
                                                {{ item.docBreadCrumb }}
                                            </span>
                                        </td>
                                        <td class="text-center">
                                            <span class="text-center text-overflow-dynamic-container">
                                                {{ convertdateformat(item.created_at) }}
                                            </span>
                                        </td>
                                        <td class="text-center">
                                            <span class="text-center text-overflow-dynamic-container">
                                                {{ convertmbtoKb(item.DocSize) }}
                                            </span>
                                        </td>
                                        <td class="text-center">
                                            <span class="text-center" style="align-item:center">
                                                {{ item.RetentionDays }}
                                            </span>
                                        </td>
                                        <td class="text-center">
                                            <a class="text-center" id="aShareFolder"
                                                @click="RestoreFiles(item.Id, item.IsDocCode)" title="Restore"
                                                data-toggle-tooltip="tooltip" data-original-title="Share">
                                                <em class="fa fa-recycle" style="font-size: 18px;"></em>
                                            </a>&nbsp;
                                            <a class="text-center" @click="DeleteSourceFiles(item.Id, item.IsDocCode)"
                                                id="aShareFolder" title="Delete" data-toggle-tooltip="tooltip"
                                                data-original-title="Share">
                                                <em class="fa fa-trash" style="font-size: 18px;"></em>
                                            </a>
                                        </td>
                                    </tr>
                                    <tr>
                                        <td v-if="recyleBinData.length == 0" colspan="7"
                                            class="text-danger text-center no-record">No Record found</td>
                                    </tr>
                                </tbody>
                            </table>

                        </div>
                        <Pager v-bind:numberofrecords="totalRecords" prev-text="Prev" first-text="First"
                            v-on:SetCurrentPage="pagerMethod" last-text="Last" next-text="Next"></Pager>
                    </div>

                </div>
            </section>
        </section>
    </div>
</template>
<style scoped>
.action-list-box {
    opacity: 0;
    height: 0px;
    transition: opacity 1s;
}

.is-visible {
    display: inline-block;
    height: auto;
    overflow: hidden !important;
    height: 46px;
}
</style>
<script>
import DataService from "../../services/DataService";
import LeftMenu from "@/components/LeftMenu.vue"
import Pager from "../../components/Common/Pager.vue"
import ".././../assets/common/vue-common"
export default {
    components: {
        LeftMenu,
        Pager
    },
    data() {
        return {
            isLoading: false,
            loggeduserid: null,
            SharedFolderParent: [],
            FindalFolderArray: [],
            folderTreeList: [],
            sharedFolder: [],
            folderiddata: 0,
            pageNo: 1,
            pageSize: 10,
            recyleBinData: [],
            totalRecords: 0,
            TotalPages: "",
            recentFolderList: [],
            pinnedFolderList: [],
            RecentFolderArray: [],
            pinnedFolderArray: [],
            organizationSpace: [],
            restoredJsonData: [],
            specificSortClick: '',
            listCode: null,
            sortExp: '',
            docvalue: null,
            restoredJson: [],
            searchText: "",
            sharedparentfolder: [],
            sharedChildFolder: [],
            searchCondition: '',
            SharedUserList: []
        };
    },
    updated: function () {
        this.CheckBoxBootstrap();
        this.BindActionButtonEvent();
    },
    mounted: function () {

        $("#FolderFullTree").treeview();
        $("body").on("click", "#FolderFullTree .folderlink", function () {
            $("#FolderFullTree .folderlink").removeClass("selectedfolder");
            $(this).addClass("selectedfolder");
        });
    },
    created: function () {
        // debugger;
        this.loggeduserid = this.GetUserInfo.ID;
        this.GetSharedFolder();
        this.GetFullTree();
        this.GetRecyleBinListing();
    },
    methods: {
        convertmbtoKb: function (val) {
            var convertKb = 0;
            if (val == 0) {
                convertKb = '0 Bytes'
            } else
                if (val < 1) {
                    convertKb = val.toFixed(2) + ' KB'
                }
                else {
                    convertKb = val.toFixed(2) + ' MB'
                }
            return convertKb;
        },
        ExpandCollapseList() {
            var element1 = document.getElementById("leftmenutree");
            element1.classList.toggle("left-menu-tab-collapsed");
            var element2 = document.getElementById("rightMenuRecycle");
            element2.classList.toggle("right-content-expand");
        },
        GetFullTree() {
            var vm = this;
            vm.FindalFolderArray = []
            vm.isLoading = true;
            DataService.FolderFullTreeView().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == this.loggeduserid && value.IsGlobal == 0) {
                        var CheckIfExist = vm.FindalFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });

                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getChildFolder(folderId, value, response.data.folderTreeList)
                                vm.FindalFolderArray.push(rslt)
                                vm.GetRecentlyAddPinnedFolder();
                                vm.GetOrganizationSpace();
                            }
                        }
                    }
                });
                vm.folderTreeList = response.data.folderTreeList;
                vm.SharedUserList = response.data.userList;
                vm.sharedFolder = response.data.sharedTreeList;
                vm.isLoading = false;
            });
        },
        convertdateformat: function (date) {
            const event = new Date(date);
            return event.toISOString().slice(0, 10).replace("-", "/").replace("-", "/");
        },
        /*
        This method is used to get child folder in "My Sapce"
       */
        getChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        GetSharedFolder: function () {
            var vm = this;
            DataService.GetSharedFolderTree().then((response) => {
                vm.sharedparentfolder = response.data.SharedFolderData;
                vm.sharedparentfolder.forEach((value, index) => {
                    if (value.SHARED_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.sharedChildFolder.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });
                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.sharedChildFolder.push(rslt)
                            }
                        }

                    }

                });

            });
        },
        /*
        This method is used to Delete File Permanently
       */
        DeleteSourceFiles: function (id, doccode, chkBtn) {
            var vm = this;
            var val = "";
            vm.restoredJsonData = [];
            let obj = {};
            if (id > 0) {
                val = id;
                obj = {
                    SourceId: val,
                    RefCode: doccode
                }
                vm.restoredJsonData.push(obj);
            }
            if (chkBtn == 'Empty') {
                $('#tblRecycleBinFileList .chkItems:checkbox').not("[id^='chkAll']").each(function () {
                    var doccode = $(this).attr("doccode")
                    val = $(this).val();
                    obj = {
                        SourceId: val,
                        RefCode: doccode
                    }
                    vm.restoredJsonData.push(obj);
                });
            }
            else {
                $('#tblRecycleBinFileList .chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                    var doccode = $(this).attr("doccode")
                    val = $(this).val();
                    obj = {
                        SourceId: val,
                        RefCode: doccode
                    }
                    vm.restoredJsonData.push(obj);
                });
            }
            var params=JSON.stringify(vm.restoredJsonData)
            vm.confirmR("Are you sure you want to permanently delete this record ?", true, (vm.$t("Warning") + "  " + vm.$t("Doc Storage Source")), function () {
                vm.isLoading = true;
                DataService.DeleteStorageFilePermanently(JSON.stringify(params)).then((response) => {
                    if (response.data == 1) {
                        vm.ShowAlert("Record has been deleted successfully.");
                        vm.restoredJsonData = [];
                        vm.GetRecyleBinListing();
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                    vm.isLoading = false;
                })
            })

        },
        EmptyBin: function (id, doccode, chkBtn) {
            var vm = this;
            var val = "";
            vm.restoredJsonData = [];
            let obj = {};
            if (id > 0) {
                val = id;
                obj = {
                    SourceId: val,
                    RefCode: doccode
                }
                vm.restoredJsonData.push(obj);
            }
            var params = `folderId=${vm.folderiddata}&pageNo=${vm.pageNo}&pageSize=${vm.totalRecords}&refCode=Recylebin&SearchFilter=&sortBy=${vm.specificSortClick}&sortExp=${vm.sortExp}`;
            DataService.GetFolderList(params).then((response) => {
                vm.recyleBinData = response.data.FolderData;
                if(vm.recyleBinData.length>0){
                    vm.totalRecords = vm.recyleBinData[0].totalRecords;
                }
                else{
                    $("#EmptyBin").addClass("disabled");
                }
                vm.TotalPages = Math.ceil(vm.totalRecords / vm.pageSize);                
            });
            vm.confirmR("Are you sure you want to permanently delete this record ?", true, (vm.$t("Warning") + "  " + vm.$t("Doc Storage Source")), function () {
                vm.isLoading = true;
                $('#tblRecycleBinFileList .chkItems:checkbox').not("[id^='chkAll']").each(function () {
                    var doccode = $(this).attr("doccode")
                    val = $(this).val();
                    obj = {
                        SourceId: val,
                        RefCode: doccode
                    }
                    vm.restoredJsonData.push(obj);
                });
                var params=JSON.stringify(vm.restoredJsonData)
                DataService.DeleteStorageFilePermanently(JSON.stringify(params)).then((response) => {
                    if (response.data == 1) {
                        vm.ShowAlert("Record has been deleted successfully.");
                        vm.restoredJsonData = [];
                        vm.GetRecyleBinListing();
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                    vm.isLoading = false;
                })
            })

        },
        /*
      This method is used to Pagination
     */
        pagerMethod: function (s) {            
            var vm = this;
            vm.pageSize = s.PageSize;
            vm.GetRecyleBinListing(parseInt(s.PageNumber));
        },
        SearchRecycleBinData: function (searchCond) {
            this.searchText = searchCond;
            var vm = this;
            vm.GetRecyleBinListing(vm.pageNo == NaN ? vm.pageNo : 1, this.searchText.trim())
        },
        RefreshPage: function () {
            var vm = this;
            this.$router.push({ path: "/RecyleBin" });
            window.location.reload("/RecyleBin");
        },
        onChange: function () {
            var checkCount = $('.chkItems:checked').not("[id^='chkAll']").length;
            if (checkCount > 0) {
                $('#Restore').removeClass('disabled');
                $('#delete').removeClass('disabled');

            }
            else {
                $('#Restore').addClass('disabled');
                $('#delete').addClass('disabled');
            }
        },
        SortData: function (ev, sortBy, className, name) {
            var vm = this;
            if (name == "My Space") {
                vm.listCode = "MySpace"
            }
            else if (name == "RecycleBin") {
                vm.listCode = "RecycleBin"
            }
            var clases = ev.target.getAttribute('class');
            vm.specificSortClick = sortBy;
            if (clases == 'sort' || clases == 'sort tb_headerSortUp') {
                vm.sortExp = 'ASC'
            }
            if (clases == 'sort tb_headerSortDown') {
                vm.sortExp = 'DESC'
            }
            vm.GetRecyleBinListing();
        },
        /*
      This method is used to Get Recycle Bin Listing
     */
        GetRecyleBinListing: function (page, searchCondition) {
            var vm = this;
            vm.isLoading = true;
            vm.totalRecords=0;
            if (searchCondition == undefined) {
                searchCondition = "";
            }
            if (page != null) {
                vm.pageNo = page
            }
            var params = `folderId=${vm.folderiddata}&pageNo=${vm.pageNo}&pageSize=${vm.pageSize}&refCode=Recylebin&SearchFilter=${searchCondition}&sortBy=${vm.specificSortClick}&sortExp=${vm.sortExp}`;
            DataService.GetFolderList(params).then((response) => {
                vm.recyleBinData = response.data.FolderData;
                if(vm.recyleBinData.length>0){
                    vm.totalRecords = vm.recyleBinData[0].totalRecords;
                }
                else{
                    $("#EmptyBin").addClass("disabled");
                }
                vm.TotalPages = Math.ceil(vm.totalRecords / vm.pageSize);                
                $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                $("[id^='Restore']").removeClass('enable').addClass('disabled');
                $("[id^='delete']").removeClass('enable').addClass('disabled');

                setTimeout(function () {
                    vm.ResponsiveDataTable('tblRecycleBinFileList')
                }, 1000);
                setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                vm.isLoading = false;
            });
        },
        /*
       This method is used to Recently Added or pinned folder hierarchy
      */
        GetRecentlyAddPinnedFolder: function () {
            var vm = this;
            DataService.GetRecentlyAndPinnedFolderTreeData().then((response) => {
                vm.recentFolderList = JSON.parse(response.data).RecentFolderList
                vm.pinnedFolderList = JSON.parse(response.data).PinnedFolderList
                vm.recentFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.RecentFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });
                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.RecentFolderArray.push(rslt)
                            }
                        }

                    }

                });
                vm.pinnedFolderList.forEach((value, index) => {
                    if (value.OWNER_USER_ID == vm.loggeduserid) {
                        var CheckIfExist = vm.pinnedFolderArray.filter(function (el) {
                            return el.FOLDER_ID == value.FOLDER_ID;
                        });


                        if (CheckIfExist.length == 0) {
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.pinnedFolderArray.push(rslt)
                            }
                        }

                    }

                });
            });

        },
        /*
       This method is used to Global space hierarchy
      */
        GetOrganizationSpace: function () {

            var vm = this;
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });


                    if (CheckIfExist.length == 0) {
                        if (value.FOLDER_PARENT_ID == null) {
                            let folderId = value.FOLDER_ID
                            let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                            vm.organizationSpace.push(rslt)
                        }
                    }
                });
            });


        },
        getsharedChildFolder: function (folderId, currentItem, data) {
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.FOLDER_PARENT_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        /*
       This method is used to restore files or folder
      */
        RestoreFiles: function (id, docCode) {
            var vm = this;

            var val = "";
            if (id > 0) {
                val = id;
            }
            $('#tblRecycleBinFileList .chkItems:checked').not("[id^='chkAll']").each(function () {


                if (val.length > 0) {
                    val += ',';
                }
                if (docCode != null) {
                    vm.docvalue = docCode;
                }
                else {
                    vm.docvalue = $(this).attr("doccode")
                }
                val = $(this).val();
                let obj = {
                    SourceId: val,
                    RefCode: vm.docvalue
                }
                vm.restoredJson.push(obj);
            });
            if (docCode != null) {
                let obj = {
                    SourceId: val,
                    RefCode: docCode
                }
                vm.restoredJson.push(obj);
            }
            let restoredJsonData = JSON.stringify(vm.restoredJson);
            var params = `jsonSourceIds=${restoredJsonData}`;
            vm.confirmR(vm.$t("ConfirmRestoreecord"), true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                DataService.RestoreItemsFromRecycleBinList(params).then((response) => {
                    if (response.data == 1) {
                        vm.confirmR("Are you sure you want to replace this record.", true, (vm.$t("Delete") + "  " + vm.$t("Groups")), function () {
                            let obj = {
                                SourceId: val,
                                RefCode: vm.docvalue
                            }
                            vm.restoredJson.push(obj);
                            let restoredJsonData = JSON.stringify(vm.restoredJson);
                            var params = `jsonSourceIds=${restoredJsonData}&IsRestored=1`;
                            DataService.RestoreItemsFromRecycleBinList(params).then((response) => {
                                if (response.data == 2) {
                                    vm.ShowAlert("Record has been successfully replaced");
                                    vm.GetRecyleBinListing();
                                }
                            })
                        })
                    }
                    else if (response.data == 3) {
                        vm.ShowAlert("File has been restored successfully");
                        vm.GetRecyleBinListing();
                    }
                    else if (response.data == 0) {
                        vm.ShowAlert("Folder has been restored successfully");
                        vm.GetRecyleBinListing();
                    }
                    else {
                        vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t('Alert'));
                    }
                })
            })

        },
        GetFileIconClass: function (ext) {
            ext = ext.toLowerCase();
            var fileclass = "";
            if (ext == 'png' || ext == "jpeg" || ext == "jpg" || ext == "JPG" || ext == "jfif" || ext == "pdf" || ext == "xlsx" || ext == "docx" || ext == "pptx") {
                fileclass='d-none';
            }
            else if (ext == "txt") {
                fileclass = "fa fa-file-text-o"
            }
            else if (ext == "xls") {
                fileclass = "fa fa-file-excel-o"
            }
            else if (ext == "mp3") {
                fileclass = "fa fa-file-audio-o"
            }
            else if (ext == "mp4") {
                fileclass = "fa fa-file-video-o"
            }
            else {
                fileclass = "fa fa-file-o"
            }
            return fileclass
        },
        GetThumbnail: function (path) {
            var cls = null;
            if (path != null) {
                var incStr = path.includes("wwwroot");
            }
            if (incStr == true) {
                cls = path.replace("/wwwroot", "");
            }
            else {
                cls = path;
            }
            return cls;
        },
    }

}
</script>

